<template>
  <div>
    <div class="grid">
      <div v-for="(row, rowIndex) in field" :key="rowIndex" class="row">
        <div v-for="(cell, cellIndex) in row" :key="cellIndex" class="cell" :class="{ alive: cell === 1 }"></div>
      </div>
    </div>
    <div v-if="field.length === 0" class="game-over">Game over</div>
    <button @click="startGame">Start</button>
    <button v-if="isRunning" @click="pauseGame">Pause</button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      field: [],
      isRunning: false, // Флаг для отслеживания состояния игры (запущена или остановлена)
      intervalId: null, // Идентификатор интервала для остановки игры
    };
  },
  methods: {
    getFieldState() {
      const apiUrl = 'http://0.0.0.0:5000/game_of_life'; // Замените на адрес вашего сервера API
      axios.get(apiUrl)
        .then(response => {
          this.field = response.data;
          if (this.field.length === 0) {
            clearInterval(this.intervalId); // Останавливаем игру, если пришел пустой список
          }
        })
        .catch(error => {
          console.error(error);
        });
    },
    startGame() {
      this.getFieldState(); // Выполняем запрос состояния при старте игры
      this.isRunning = true; // Устанавливаем флаг запущенной игры
      this.intervalId = setInterval(() => {
        this.getFieldState();
      }, 1000);
    },
    pauseGame() {
      clearInterval(this.intervalId); // Останавливаем интервал
      this.isRunning = false; // Устанавливаем флаг остановленной игры
    },
  },
};
</script>

<style>
.grid {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
}

.cell {
  width: 10px;
  height: 10px;
  border: 1px solid #ccc;
}

.cell.alive {
  background-color: #000;
}

.game-over {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  height: 100px;
}
</style>
